.card12{
    /* height: 300px; */
    background:url("https://64.media.tumblr.com/3033b53e473be51c6b963fcde4a52ccf/tumblr_nblocyQbfM1ts4htvo1_500.gif");
    border-radius: 30px;
    margin-left: 10px;
    width: 100%;
    color: white;
    margin: auto;
    text-align: center;
    padding-top: 40px;
    padding-bottom: 40px;
}
.card12 h1{
    font-size: 50px;
}
.InputGroup{
    display: grid;
    grid-template-columns: 20% 20% 20% 20%;
    justify-content: space-between;
}
input {
    font-size: 20px;
    display: block;
    width: 100%;
    height: 50px;
    padding: 5px 10px;
    background: none;
    background-image: none;
    border: 1px solid #a0b3b0;
    color: #ffffff;
    background: #757575;
    border-radius: 0;
    border-radius: 10px;
    transition: border-color 0.25s ease, box-shadow 0.25s ease;
    margin-bottom: 20px;
  }
  input:focus {
    outline: 0;
    border-color: #1ab188;
  }


